exports.push([module.id, "\n.avatar[data-v-966533d6] {\n  margin-top: -70px;\n  padding-bottom: 20px;\n}\n.avatar img[data-v-966533d6] {\n  padding: 1rem;\n  background: #fff;\n  width: 128px;\n  height: 128px;\n  border-radius: 50%;\n  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);\n}\ninput[data-v-966533d6] {\n  font-weight: 300;\n}\np[data-v-966533d6] {\n  font-weight: 700;\n}\np.subtitle[data-v-966533d6] {\n  padding-top: 1rem;\n}\na[data-v-966533d6] {\n  text-decoration: none;\n}\n\n", "",{"version":3,"sources":["/Users/voz/Workspace/teachee-news-v2/app/javascript/components/app/javascript/components/SignIn.vue"],"names":[],"mappings":";AAgMA;EACA,iBAAA;EACA,oBAAA;AACA;AAEA;EACA,aAAA;EACA,gBAAA;EACA,YAAA;EACA,aAAA;EACA,kBAAA;EAGA,4EAAA;AACA;AAEA;EACA,gBAAA;AACA;AAEA;EACA,gBAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,qBAAA;AACA","file":"SignIn.vue?vue&type=style&index=0&id=966533d6&scoped=true&lang=css&","sourcesContent":["<template>\n  <div class=\"app-container\">\n    <front-menu/>\n\n    <section class=\"hero is-success is-fullheight\">\n      <div class=\"hero-body\">\n        <div class=\"container\">\n          <div class=\"column is-4 is-offset-4\">\n            <!--<h3 class=\"title has-text-grey\">Sign in</h3>-->\n            <p class=\"subtitle has-text-grey\">.</p>\n            <div class=\"box\">\n              <figure class=\"avatar\">\n                <picture>\n                  <source srcset=\"/static/logo_teachertool_v2_square.webp\" type=\"image/webp\"  />\n                  <source srcset=\"/static/logo_teachertool_v2_square.png\" type=\"image/png\"  />\n                  <img alt='Teachee'  role='presentation'  src='/static/logo_teachertool_v2_square.png'  />\n                </picture>\n              </figure>\n              <div\n                v-google-signin-button=\"googleSignInParams.client_id\"\n                class=\"button is-primary is-medium is-fullwidth google-signin-button\"\n                @click.native=\"googleIsLoading = true\">\n                Sign up with Google\n              </div>\n              <p class=\"has-text-centered\">\n                or\n              </p>\n              <form>\n\n                <div\n                  v-if=\"error\"\n                  class=\"message is-danger\">\n                  <div\n                    class=\"message-body\">\n                    <span v-html=\"error\"></span>\n                    <router-link\n                      v-if=\"error && error.indexOf('Invalid username and password') !== -1\"\n                      :to=\"{name: 'SignUp'}\">Sign up</router-link>\n                  </div>\n                </div>\n\n                <b-field>\n                  <b-input\n                    id=\"email\"\n                    v-model=\"form.email\"\n                    type=\"text\"\n                    required\n                    autofocus\n                    size=\"is-medium\"\n                    placeholder=\"Email\"\n                  />\n                </b-field>\n\n                <b-field>\n                  <b-input\n                    id=\"password\"\n                    v-model=\"form.password\"\n                    type=\"password\"\n                    required\n                    password-reveal\n                    size=\"is-medium\"\n                    placeholder=\"Password\"\n                  />\n                </b-field>\n\n                <b-field class=\"has-text-left\">\n                  <b-checkbox v-model=\"form.checked\">\n                    Remember me\n                  </b-checkbox>\n                  <!--<a style=\"float: right\" href=\"/\">login?</a>-->\n                </b-field>\n\n\n                <button\n                  id=\"btn_sign_in\"\n                  :class=\"{'is-loading': isLoading, 'is-disabled': isLoading}\"\n                  class=\"button is-block is-primary is-outlined is-medium is-fullwidth\"\n                  @click=\"onSubmit($event)\">\n                  Sign in\n                </button>\n              </form>\n            </div>\n            <p class=\"has-text-grey\">\n              <router-link :to=\"{name: 'SignUp'}\">Sign up</router-link> &nbsp;·&nbsp;\n              <router-link :to=\"{name: 'LostPasswordPage'}\">Forgot Password</router-link> &nbsp;·&nbsp;\n              <a href=\"mailto:guillaume@materialmaker.io\">Help!</a>\n            </p>\n          </div>\n        </div>\n      </div>\n    </section>\n  </div>\n\n</template>\n\n\n<script>\n\nimport GoogleSignInButton from 'vue-google-signin-button-directive'\n\nexport default {\n  name: \"SignIn\",\n  directives: {\n    GoogleSignInButton\n  },\n  data() {\n    return {\n      gdprConcented: false,\n      error: null,\n      googleIsLoading: false,\n      redirect: null,\n      form: {\n        pid: '',\n        email: \"\",\n        username: \"\",\n        password: \"\",\n        confirm_password: \"\",\n        checked: false\n      },\n      googleIsLoading: false,\n      googleSignInParams: {\n        client_id: '476667578043-0tm7eoufgmi6m961qb2m77hhelj5ksl5.apps.googleusercontent.com',\n        response_type: 'permission'\n      }\n    };\n  },\n  created() {\n    this.form.pid = this.$cookie.get('pid')\n  },\n\n  methods: {\n    onSubmit(evt) {\n      this.isLoading = true;\n      evt.preventDefault();\n      const router = this.$router;\n      this.$auth\n        .login({\n          body: this.form,\n          redirect: this.redirect || { name: \"FrontPage\" },\n          rememberMe: this.form.checked\n        })\n        .then(\n          (res) => {\n            this.$store.dispatch(\"setUser\", res.body.data);\n            // router.push('/')\n            // this.$store.dispatch('setLayout', 'logged-in-layout').then(function(){\n            //   router.push('/')\n            // })\n          },\n          (res) => {\n            console.log(\"error \" + this.context);\n            this.error = res.data.message;\n            console.log(\"error \", this.error);\n            this.isLoading = false;\n          }\n        );\n\n    },\n    onClick(e) {\n      e.preventDefault();\n      this.$router.push(\"/\");\n    },\n    OnGoogleAuthSuccess(access_token) {\n      this.$store.dispatch(\"setUserFromGoogle\", {\n        access_token,\n        pid: this.form.pid,\n      }).then((res) => {\n        if (!res.userExisted) {\n          this.$mixpanel.alias(res.user.id+'');\n        }\n        this.$store.dispatch(\"setUser\", res.user);\n        this.$auth.user(res.user)\n        this.$auth.token(null, res.token)\n        this.$auth.watch.authenticated = true\n        this.$auth.watch.loaded = true\n        this.googleIsLoading = false;\n        // this.$store.dispatch(\"setLayout\", \"logged-in-layout\");\n        this.$cookie.set('rememberMe', true, 365)\n        // this.$router.push({name: 'Home'})\n        this.$router.push({name: 'FrontPage'})\n      });\n    },\n    OnGoogleAuthFail(error) {\n      console.log(error)\n      this.googleIsLoading = false;\n    }\n  }\n};\n</script>\n\n\n<style scoped>\n.avatar {\n  margin-top: -70px;\n  padding-bottom: 20px;\n}\n\n.avatar img {\n  padding: 1rem;\n  background: #fff;\n  width: 128px;\n  height: 128px;\n  border-radius: 50%;\n  -webkit-box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1),\n    0 0 0 1px rgba(10, 10, 10, 0.1);\n  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);\n}\n\ninput {\n  font-weight: 300;\n}\n\np {\n  font-weight: 700;\n}\n\np.subtitle {\n  padding-top: 1rem;\n}\n\na {\n  text-decoration: none;\n}\n\n</style>\n"]}]);

